<template>
    <div>
   

        <div class="row">
            <div class="col-12">
                <div class="page-title-box d-flex align-items-center justify-content-between">
                    <h4 class="mb-0 font-size-18">Cadastro de Cambios</h4>
                    <div class="page-title-right">
                            <a href="#modal-editar" @click="novoCambio()" class="btn green-jungle btn-md" data-toggle="modal">
                            <i class="fa fa-plus-circle"></i>
                            <span class="hidden-xs">Nova Combinação de Cambio</span>
                        </a>
                    </div>

                </div>
            </div>
        </div>
        <!-- end page title -->
        <div class="row" style="padding:10px">
            <div class="col-sm-12 col-md-3">                  
                <label >Mostrar  
                <select class="custom-select custom-select-sm form-control form-control-sm" v-model="filtro.per_page" @input="debounceBusca">
                    <option :value="10">10</option>
                    <option :value="25">25</option>
                    <option :value="50">50</option>
                    <option :value="100">100</option>
                </select>
                </label>                  
            </div>
            <div class="col-sm-12 col-md-7">
                <label style="width:90%">Buscar por:
                <input type="text" class="form-control form-control-sm" placeholder="Nome / Descrição" v-model="filtro.texto" @input="debounceBusca" >
                </label>                  
            </div>
            <div class="col-sm-12 col-md-2">
                <label style="width:90%">Filtrar:
                <button @click="navegar(1)" class="btn btn-outline-primary btn-sm btn-block"><span class="bx bx-search-alt"></span></button>
                </label>
            </div>
        </div>
        <div class="row">
            <div class="col-12">
                <div class="card">
                    <div class="card-body">
                        <h4 class="card-title">Lista de Cambios</h4>
                        <div class="table-responsive">
                                <table class="table mb-0" >
                                <thead>
                                <tr>
                                    <th> Id </th>
                                    <th> Moeda  </th>
                                    <th> Fator  </th>
                                    <th> Moeda  </th>
                                    <th class="text-right"> Ações  </th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="registro in cambios" :key="registro.id">
                                    <td>{{registro.id}}</td>
                                    <td>{{registro.moeda_origem.nome}} </td>
                                    <td>{{registro.multiplicar ? 'Multiplicar' : 'Dividir'}}</td>
                                    <td>{{registro.moeda_destino.nome}} </td>
                                    <td class="text-right"> 
                                        
                                        <button type="button" class="btn btn-outline-warning waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Editar"
                                                data-toggle="modal" data-target="#modal-editar" data-backdrop="static" @click="editarCambio(registro)">
                                            <i class="mdi mdi-pencil d-block font-size-12"></i>
                                        </button>
                                    </td>   
                                </tr>
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td colspan="4"> 
                                            <paginacao-componente :source="paginacao" @navigate="navegar($event)"></paginacao-componente>
                                        </td>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>

                    </div>
                </div>
            </div> <!-- end col -->
        </div> <!-- end row -->

    <div class="modal fade" id="modal-editar" tabIndex="1">
        <div class="modal-dialog modal-lg " role="document">
            <form class="modal-content" @submit.prevent="salvarCambio()">
                <div class="modal-header">
                    <h4 class="modal-title mt-0">  Cadastro de Cambio</h4>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">×</span>
                    </button>
                </div>
                <div class="modal-body" style="min-height: 450px">
                    <div class="row" >
                        <div class="col-md-6">
                            <container-componente label="Moeda 1" :validator="$v.cambioModal.moeda_origem_id">
                                <vue-select :disabled="cambioModal.id > 0" :options="moedas" :reduce="m => m.id" :clearable="false"
                                        v-model="cambioModal.moeda_origem_id" label="nome" @input="setMoeda1()">
                                </vue-select>
                            </container-componente>
                        </div>
                        <div class="col-md-6">
                            <container-componente label="Moeda 2" :validator="$v.cambioModal.moeda_destino_id">
                                <vue-select :disabled="cambioModal.id > 0" :options="moedas" :reduce="m => m.id" :clearable="false"
                                        v-model="cambioModal.moeda_destino_id" label="nome" @input="setMoeda2()">
                                </vue-select>
                            </container-componente>
                        </div>
                        <div class="col-md-6">
                            <container-componente label="Multiplicador" :validator="$v.cambioModal.multiplicar">
                                <vue-select :options="multiplicadores" :reduce="m => m.id" :clearable="false"
                                        v-model="cambioModal.multiplicar" label="label">
                                </vue-select>
                            </container-componente>
                        </div>

                        <div class="col-md-6">
                            <container-componente label="Taxa Inicial" :validator="$v.cambioModal.taxa_inicial">
                                <money v-bind="money" class="form-control" v-model="cambioModal.taxa_inicial" ></money>
                            </container-componente>
                        </div>

                        <div class="col-md-12">
                            <hr>
                            <h4><strong>Simulador</strong></h4>
                        </div>

                        <div class="col-md-4">
                            <label for="valor_1">Valor em: {{moeda1}}</label>
                            <money v-bind="money" class="form-control" v-model="valor_moeda1" ></money> 
                        </div>

                        <div class="col-md-4">
                            <label for="taxa_1">Taxa</label>
                            <money v-bind="money" class="form-control" v-model="taxa1" ></money>
                        </div>

                        <div class="col-md-4">
                            <label for="convertido_2">Valor Convertido em: {{moeda2}}</label>
                            <money v-bind="money" class="form-control" :disabled="true" v-model="convertido_moeda2" ></money> 
                        </div>

                        <div class="col-md-4">
                            <label for="valor_2">Valor em: {{moeda2}}</label>
                            <money v-bind="money" class="form-control" v-model="valor_moeda2" ></money> 
                        </div>

                        <div class="col-md-4">
                            <label for="taxa_2">Taxa</label>
                            <money v-bind="money" class="form-control" v-model="taxa2" ></money>
                        </div>

                        <div class="col-md-4">
                            <label for="convertido_1">Valor Convertido em: {{moeda1}}</label>
                            <money v-bind="money" class="form-control" :disabled="true" v-model="convertido_moeda1" ></money> 
                        </div>
                        <div class="col-md-12" v-if="erroEditar || sucessoEditar">
                            <hr>
                            <mensagem-componente :erros="erroEditar" @fechar="erroEditar=null"></mensagem-componente>
                            <mensagem-componente :sucesso="sucessoEditar" @fechar="sucessoEditar=null"></mensagem-componente>
                        </div>

                    </div>
                </div>
                 
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">Fechar</button>
                    <button type="submit" class="btn btn-primary" v-if="!sucessoEditar">
                        <i class="fa fa-save"></i> Salvar
                    </button>
                </div>  
            </form>
        </div>
    </div>

</div>

    
</template>

<script>
import { CambioService } from '../../../services'
import { required, minValue} from "vuelidate/lib/validators";
const cambioService = new CambioService();
export default {
    
    data(){
        return{
            cambios: [],
            moedas: [],
            timer:null,
            multiplicadores: [
                { id: 0, label:'Dividir'},
                { id: 1, label:'Multiplicar'},
            ],
            //simulador
            moeda1: {},
            moeda2: {},
            taxa1: '',
            taxa2: '',
            valor_moeda1: '',
            valor_moeda2: '',
            convertido_moeda1: '',
            convertido_moeda2: '',
            filtro:{
                ordenar: 'id',
                ordem: 'asc',
                page: 1,
                per_page: 25,
                texto: '',
            },
            paginacao: {
                current_page: 0,
                total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 25
            },
            debounce: null,
            cambioModal: {
                id: null,
                moeda_origem_id: '',
                moeda_destino_id: '',
                multiplicar: '',
                taxa_inicial: ''
            },
            sucessoEditar: null,
            erroEditar: null,
            money: {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: '',
                precision: 2,
                masked: false
            }
        }   
    },
    validations() {
        return {
            cambioModal: {
                moeda_origem_id: { required},
                moeda_destino_id: { required},
                multiplicar: { required },
                taxa_inicial: { required, minValue: minValue(0.01) }
            },
        }
    },
    methods:{
        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscarCambios:function(){
            cambioService.get(this.filtro).then(
                response => {
                    this.cambios = response.data.data;
                    this.paginacao = response.data;
                    this.filtro.page = response.data.current_page;
                    this.paginacao.per_page = response.data.per_page;
                }
            )
        },
        novoCambio: function(obj){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.$v.cambioModal.$reset();
            
            this.cambioModal = {
                id: null,
                moeda_origem_id: '',
                moeda_destino_id: '',
                multiplicar: '',
            };
            this.moeda1 = '';
            this.moeda2 = '';
            this.taxa1 = '';
            this.taxa2 = '';
            this.valor_moeda1 = '';
            this.valor_moeda2 = '';
            this.convertido_moeda1 = '';
            this.convertido_moeda2 = '';
            
        },
        editarCambio:function(obj){
            this.$v.cambioModal.$reset();
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.cambioModal = {...obj};
            this.setMoeda1();
            this.setMoeda2();
            this.taxa1 = '';
            this.taxa2 = '';
            this.valor_moeda1 = '';
            this.valor_moeda2 = '';
            this.convertido_moeda1 = '';
            this.convertido_moeda2 = '';
        },
        salvarCambio: function(){
            this.$v.cambioModal.$touch();
            if(this.$v.cambioModal.$invalid){
                return 0;
            }
            this.sucessoEditar = null;
            this.erroEditar = null;
            let req = this.cambioModal.id ? cambioService.update(this.cambioModal) : cambioService.create(this.cambioModal);
            req.then( response => {
                
                let obj = response.data;
                
                let i = this.cambios.map(x => x.id).indexOf(obj.id);
                // if(i < 0)
                //     this.cambios.push(obj);
                // else
                //     this.cambios.splice(i,1,obj)
                this.buscarCambios();
                
                this.sucessoEditar = 'Conta salva com sucesso!';
            }).catch( erro => this.erroEditar = erro)            
        },
        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscarCambios();
        },
        buscarMoedas: function(){
            
            cambioService.todasMoedas().then(
                response => {                    
                    this.moedas = response.data;
                }
            )
        },
        setMoeda1: function(){
            let moeda1 = this.moedas.filter(x => x.id == this.cambioModal.moeda_origem_id);
            if(moeda1.length == 1)
                this.moeda1 = moeda1[0].nome;
            else
                this.moeda1 = '';
        },
        setMoeda2: function(){
            let moeda2 = this.moedas.filter(x => x.id == this.cambioModal.moeda_destino_id);
            if(moeda2.length == 1)
                this.moeda2 = moeda2[0].nome;
            else
                this.moeda2 = '';
        }
        
    },
    watch:{
        valor_moeda1: function(val){
            if( !(this.valor_moeda1 > 0) || !(this.taxa1 > 0) )
                this.convertido_moeda2 = 0;
            else
                this.convertido_moeda2 = (this.cambioModal.multiplicar == 1) ? this.taxa1 * this.valor_moeda1 : this.valor_moeda1 / this.taxa1;
        },
        taxa1: function(val){
            if( !(this.valor_moeda1 > 0) || !(this.taxa1 > 0) )
                this.convertido_moeda2 = 0;
            else
                this.convertido_moeda2 = (this.cambioModal.multiplicar == 1) ? this.taxa1 * this.valor_moeda1 : this.valor_moeda1 / this.taxa1;
        },
        valor_moeda2: function(val){
            if( !(this.valor_moeda2 > 0) || !(this.taxa2 > 0) )
                this.convertido_moeda1 = 0;
            else
                this.convertido_moeda1 = (this.cambioModal.multiplicar == 1) ?  this.valor_moeda2 / this.taxa2 : this.taxa2 * this.valor_moeda2;
        },
        taxa2: function(val){
            if( !(this.valor_moeda2 > 0) || !(this.taxa2 > 0) )
                this.convertido_moeda1 = 0;
            else
                this.convertido_moeda1 = (this.cambioModal.multiplicar == 1) ?  this.valor_moeda2 / this.taxa2 : this.taxa2 * this.valor_moeda2;
        },
        'cambioModal.multiplicar': function(val){
            if( !(this.valor_moeda1 > 0) || !(this.taxa1 > 0) )
                this.convertido_moeda2 = 0;
            else
                this.convertido_moeda2 = (this.cambioModal.multiplicar == 1) ? this.taxa1 * this.valor_moeda1 : this.valor_moeda1 / this.taxa1;
            if( !(this.valor_moeda2 > 0) || !(this.taxa2 > 0) )
                this.convertido_moeda1 = 0;
            else
                this.convertido_moeda1 = (this.cambioModal.multiplicar == 1) ?  this.valor_moeda2 / this.taxa2 : this.taxa2 * this.valor_moeda2;
           
        }
    },
    mounted(){
        this.buscarMoedas();
        setTimeout(() => {
            this.buscarCambios();    
        }, 400);
    },
}
</script>